@import '../abstracts/variables';

header {
	position: fixed;
	width   : 100%;
	height  : 265px;
	top     : 0;
	left    : 0;
	z-index : 999;

	// LOGO
	.block-logo {
		position  : absolute;
		width     : 416px;
		height    : 100%;
		left      : 0;
		top       : 0;
		z-index   : 1;
		transition: .3s all ease-in-out;

		.logo {
			position: absolute;
			top     : 25px;
			left    : 25px;

			img {
				transition: .3s all ease-in-out;
			}
		}

		.toggle-menu {
			position : absolute;
			top      : 50%;
			right    : 0;
			transform: translateY(-50%);

			width        : 30px;
			height       : 30px;
			background   : #000000;
			border-radius: 5px;

			&:before,
			&:after {
				position        : absolute;
				left            : 7px;
				width           : 16px;
				height          : 2px;
				background-color: #ffffff;
				content         : "";
				transition      : .3s all linear;
			}

			&:before {
				top: 18px;
			}

			&:after {
				top: 10px;
			}

			&.active {

				&:before,
				&:after {
					top : 50%;
					left: 50%;
				}

				&:before {
					transform: translate(-50%, -50%) rotate(45deg);
				}

				&:after {
					transform: translate(-50%, -50%) rotate(-45deg);
				}
			}
		}

		.sologan {
			position : absolute;
			top      : 50%;
			left     : 40%;
			transform: translate(-50%, -50%) rotate(-32deg);
			width    : 100%;

			p {
				text-align : center;
				font-size  : 13px;
				font-weight: 700;
				color      : map-get($mau, main);
			}
		}

		.triangle {
			position     : absolute;
			top          : 0;
			bottom       : 0;
			width        : 0;
			height       : 0;
			z-index      : -1;
			border-left  : 0px solid transparent;
			border-bottom: 0px solid transparent;
		}
	}

	// TOP HEADER
	.top-header {
		position  : relative;
		height    : 55px;
		transition: .3s all ease-in-out;

		.social-media,
		.search,
		.block-cart {
			transition: .3s all ease-in-out;
		}

		.social-media {
			ul {
				display        : flex;
				list-style-type: none;
				padding-left   : 0;

				li {
					margin-right: 29px;

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}

		.search {
			margin-left: 69px;
			position   : relative;

			.form-group {
				width: 221px;

				.form-control {
					font-size    : 14px;
					line-height  : 27px;
					background   : rgba($color: #F9F9F9, $alpha: .5);
					border       : 0;
					width        : 100%;
					padding      : 0 1rem;
					color        : #ffffff;
					border-radius: 14px;

					&::placeholder {
						color: #ffffff;
					}
				}
			}

			.submit {
				position : absolute;
				top      : 40%;
				right    : 5px;
				transform: translateY(-50%);

				button {
					line-height: 1;
					background : none;
					border     : 0;
				}
			}
		}

		.block-cart {
			.block-text-icon {
				cursor     : pointer;
				display    : flex;
				align-items: center;
				height     : 27px;
				padding    : 0 8px;
				background : map-get($mau, main);
				margin-left: 16px;
				user-select: none;

				h5 {
					position      : relative;
					font-size     : 12px;
					color         : #ffffff;
					font-weight   : 700;
					text-transform: uppercase;
					margin-left   : 5px;
					margin-right  : 10px;
					padding-right : 5px;

					&:after {
						content   : '';
						position  : absolute;
						right     : 0;
						top       : 50%;
						transform : translateY(-50%);
						background: #ffffff;
						height    : 10px;
						width     : 1px;
					}
				}

				.cart-number {
					font-size  : 12px;
					color      : #ffffff;
					font-weight: 700;
				}
			}

			.cart-panel {
				position     : absolute;
				top          : 100%;
				right        : 0;
				width        : 350px;
				box-shadow   : 0px 0px 5px rgba(black, .1);
				background   : white;
				z-index      : 30;
				padding      : 0 12px;
				border       : 1px solid #DFDFDF;
				border-radius: 5px;
				transition   : all .3s cubic-bezier(.2, .5, .5, .8);
				opacity      : 0;
				visibility   : hidden;
				transform    : translateY(10px);

				&:after {
					content      : '';
					border-left  : 6px solid transparent;
					border-right : 6px solid transparent;
					border-bottom: 17px solid white;
					position     : absolute;
					bottom       : 100%;
					right        : 9px;
					z-index      : 2;
				}

				&:before {
					content      : '';
					border-left  : 7px solid transparent;
					border-right : 7px solid transparent;
					border-bottom: 19px solid rgba(#707070, .35);
					position     : absolute;
					bottom       : 100%;
					right        : 8px;
					z-index      : 2;
				}

				.cart-title {
					display    : flex;
					align-items: center;
					padding    : 12px 0;

					svg {
						margin-right: 10px;
					}
				}

				.cart-list {
					max-height: 214px;
					overflow-y: auto;

					&::-webkit-scrollbar {
						width     : 5px;
						background: darken(white, 12%);
					}

					&::-webkit-scrollbar-thumb {
						width     : 5px;
						background: map-get($mau, main);
					}
				}

				.cart-item {
					display      : flex;
					padding      : 5px 0;
					border-bottom: 1px solid #f5f5f5;

					.info {
						font-size: 12px;

						.name {
							overflow          : hidden;
							text-overflow     : ellipsis;
							display           : -webkit-box;
							-webkit-box-orient: vertical;
							-webkit-line-clamp: 2;
							max-height        : 36px;

							a {
								color: #1c1c1c;
							}
						}
					}

					.price {
						flex     : 0 0 100px;
						max-width: 100px;
						font-size: 16px;
						color    : map-get($mau, main);
					}
				}

				.cart-button {
					padding: 12px 0;

					.btn-apply {
						text-transform: none;
						font-size     : 15px;
						height        : 40px;
						border-radius : 5px;
						background    : map-get($mau, main);
						color         : white;
					}
				}

				@media (max-width: 1024.98px) {
					width   : 100%;
					position: fixed;
					top     : 60px;
					right   : 0;

					&:after,
					&:before {
						content: none;
					}

					.cart-list {
						max-height   : 321px;
						margin-bottom: 10px;
					}

					.cart-button {
						padding-top: 0;
					}
				}

				&.open {
					transform : translateY(0);
					opacity   : 1;
					visibility: visible;
				}
			}
		}

		&:after {
			content   : '';
			position  : absolute;
			width     : 100%;
			height    : 1px;
			left      : 0;
			bottom    : 0;
			background: rgba($color: #ffffff, $alpha: .5);
			transition: .3s all ease-in-out;
		}
	}

	// BOTTOM HEADER
	.bottom-header {
		margin-top: 12px;

		.main-nav {
			.nav-list {
				display        : flex;
				align-items    : center;
				justify-content: flex-end;
				list-style-type: none;
				padding-left   : 0;
				transition     : .3s all ease-in-out;

				.nav-item {
					margin-right: 59px;

					.nav-link {
						position      : relative;
						font-size     : 18px;
						color         : #ffffff;
						font-weight   : 700;
						text-transform: uppercase;
						transition    : .3s all ease;
						padding-bottom: 5px;

						&:after {
							content   : '';
							position  : absolute;
							left      : 50%;
							bottom    : 0;
							width     : 0;
							height    : 3px;
							background: map-get($mau, main-2);
							transform : translateX(-50%);
							transition: .3s all ease;
						}
					}

					.language {
						display: flex;

						.item {
							margin-right: 9px;
							opacity     : .3;

							&:last-child {
								margin-right: 0;
							}

							&.active {
								opacity: 1;
							}
						}
					}

					&:last-child {
						margin-right: 0;
					}

					@media (min-width: 1025px) {
						&:hover {
							.nav-link {
								color: map-get($mau, main);

								&:after {
									width: 100%;
								}
							}
						}
					}
				}
			}
		}
	}

	&.scrolled {
		height: unset;

		.block-logo {
			top      : 50%;
			transform: translateY(-50%);

			.logo {
				top      : 50%;
				transform: translateY(-50%);

				img {
					width: 50%;
				}
			}

			.sologan {
				display: none;
			}
		}

		.top-header {
			height: 0;

			.social-media,
			.search,
			&:after {
				opacity       : 0;
				pointer-events: none;
			}

			.block-cart {
				position: absolute;
				right   : 10px;
				top     : 20px;
				z-index : 10;

				.cart-panel {
					&.open {
						top: 155%;
					}
				}
			}
		}

		.bottom-header {
			background: #ffffff;
			box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: .16);
			margin-top: 0;

			.main-nav {
				.nav-list {
					height: 70px;

					.nav-item {
						.nav-link {
							color: #000;
						}

						@media (min-width: 1025px) {
							&:hover {
								.nav-link {
									color: map-get($mau, main);
								}
							}
						}
					}
				}
			}
		}
	}

	@media (max-width: 1024.98px) {
		height    : 60px;
		background: #ffffff;
		box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: .16);

		.block-logo {
			width: 130px;

			.logo {
				top      : 50%;
				transform: translateY(-50%);
				left     : 10px;

				img {
					width: 50%;
				}
			}

			.sologan {
				display: none;
			}
		}

		.top-header {
			height: 0;

			.social-media,
			.search,
			&:after {
				opacity       : 0;
				pointer-events: none;
			}

			.block-cart {
				position: absolute;
				right   : 10px;
				top     : 15px;
				z-index : 10;
			}
		}

		.bottom-header {
			position  : fixed;
			width     : 70%;
			left      : -100%;
			top       : 60px;
			bottom    : 0;
			margin-top: 0;
			background: #ffffff;
			transition: .3s all ease-in-out;

			.main-nav {
				margin-top: 25px;

				.nav-list {
					flex-direction : column;
					justify-content: flex-start;
					align-items    : flex-start;

					.nav-item {
						margin-right  : 0;
						margin-bottom : 25px;
						padding-bottom: 10px;
						width         : 100%;
						border-bottom : 1px solid rgba($color: #000000, $alpha: .3);

						.nav-link {
							font-size: 14px;
							color    : #000000;
						}

						&:last-child {
							margin-bottom: 0;
						}
					}
				}
			}

			&.active {
				left: 0;
			}
		}
	}

	@media (max-width: 767.98px) {
		.bottom-header {
			width: 80%;
		}
	}
}